<template>
  <div class="setting">
    <div class="header">
      <div class="title"></div>
    </div>
    <fieldset :disabled="changePasswordPending">
      <table>
        <thead>
          <tr>
            <th></th>
            <th class="text-left" @click="changeOrCancel">
              <button>
                <span v-if="changePasswordVisible"></span>
                <span></span>
                <span v-if="!changePasswordVisible"></span>
              </button>
            </th>
            <th>
              <span class="success" v-if="changePasswordSuccess">{{changePasswordSuccess}}</span>
              <span class="fail" v-if="changePasswordFail">{{changePasswordFail}}</span>
            </th>
          </tr>
        </thead>
        <tbody v-if="changePasswordVisible">
          <tr>
            <td></td>
            <td>
              <input
                type="password"
                v-model="password.old"
                @focus="errorPassword.old1=errorPassword.old2=changePasswordFail=false"
              >
            </td>
            <td>
              <span class="error" v-if="errorPassword.old1"></span>
              <span class="error" v-if="errorPassword.old2"></span>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <input type="password" v-model="password.new" @focus="errorPassword.new=changePasswordFail=false">
            </td>
            <td>
              <span class="error" v-if="errorPassword.new">6</span>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <input type="password" v-model="password.repeat" @focus="errorPassword.repeat=changePasswordFail=false">
            </td>
            <td>
              <span class="error" v-if="errorPassword.repeat"></span>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <button @click="validateAndChangePassword"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </fieldset>
  </div>
</template>

<script>
import store from "../../lib/store";
import api from "../../lib/api";

export default {
  data() {
    return {
      changePasswordVisible: false, //
      password: {}, //
      user: store.get("user"), //
      errorPassword: {
        //
        old1: false,
        old2: false,
        new: false,
        repeat: false
      },
      changePasswordPending: false, // 
      changePasswordSuccess: false, //
      changePasswordFail: false, //
    };
  },

  methods: {
    //
    changeOrCancel() {
      this.changePasswordVisible = !this.changePasswordVisible;
      this.password = {};
      for (const key in this.errorPassword) {
        this.errorPassword[key] = false;
      }
    },

    //
    validateAndChangePassword() {
      let user = this.user;
      let passwordOld = this.password.old;

      //  
      if (
        this.inValidateOld1() ||
        this.inValidateNew() ||
        this.inValidateRepeat()
      ) {
        return;
      }

      //
      let param = {
        id: user.id,
        only: ["password"]
      };

      api("user/find", param).then(r => {
        if (passwordOld !== r.data.password) {
          this.errorPassword.old2 = "";
          return;
        } else {
          this.changePassword();
        }
      });
    },

    //
    changePassword() {
      this.errorPassword.old = false;

      this.changePasswordPending = true;

      api("user/update", {
        id: this.user.id,
        password: this.password.new
      }).then(r => {
        if (r.success) {
          this.changePasswordPending = false;
          this.changePasswordVisible = false;
          this.password = {};
          this.changePasswordSuccess = "";
          setTimeout(() => {
            this.changePasswordSuccess = false;
          }, 2000);
        } else {
          this.changePasswordPending = false;
          this.password = {};
          this.changePasswordFail = "";  
        }
      });
    },

    //
    inValidateOld1() {
      return (this.errorPassword.old1 = !this.password.old);
    },

    inValidateNew() {
      return (this.errorPassword.new =
        !this.password.new || this.password.new.length < 6);
    },

    inValidateRepeat() {
      return (this.errorPassword.repeat =
        !this.password.repeat || this.password.repeat !== this.password.new);
    }
  }
};
</script>

