<template>
  <div id="admin">
    <div class="header">
      <div class="title"></div>
    </div>
    <div class="create-user">
      <button @click="createOrCancel=!createOrCancel; current={}; errors={}">
        <span v-if="createOrCancel == false"></span>
        <span v-else></span>
      </button>
      <div v-if="createOrCancel">
        <form @submit.prevent="createOrUpdate">
          <label @keyup="debounceValidate('username')">
            <div></div>
            <input type="text" v-model="current.username">
            <div class="error" v-for="(value, e) in errors.username" :key="e">
              <div v-if="value">{{rules.username[e].msg}}</div>
            </div>
          </label>
          <label @keyup="debounceValidate('nickname')">
            <div></div>
            <input type="text" v-model="current.nickname">
            <div class="error" v-for="(value, e) in errors.nickname" :key="e">
              <div v-if="value">{{rules.nickname[e].msg}}</div>
            </div>
          </label>
          <label @keyup="debounceValidate('password')">
            <div></div>
            <input type="password" v-model="current.password">
            <div class="error" v-for="(value, e) in errors.password" :key="e">
              <div v-if="value">{{rules.password[e].msg}}</div>
            </div>
          </label>
          <button type="submit"></button>
        </form>
      </div>
    </div>
    <div class="list">
      <table>
        <thead>
          <th>ID</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </thead>
        <tbody>
          <tr v-for="(it, index) in list" :key="index">
            <td>{{it.id}}</td>
            <td>{{it.username}}</td>
            <td>{{it.nickname || "-"}}</td>
            <td>{{it.info || "-"}}</td>
            <td>
              <button @click="createOrCancel=true; current=it"></button>
              <button @click="deleteUser(it.id)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <Pagination :total="total" :limit="readParams.limit" :onChange="filp"/>
    </div>
  </div>
</template>

<script>
import admin from "../../mixin/admin";
import api from "../../lib/api";


export default {
  mixins: [admin],
  data() {
    return {
      model: "user",
      readParams: {
        page: 1,
        limit: 4
      },
      rules: {
        username: {
          required: {
            msg: ""
          },
          unique: {
            params: ["user", "exists", "username"],
            msg: ""
          },
          lengthBetween: {
            params: [4, 12],
            msg: "412"
          },
          regex: {
            params: [/^[a-zA-Z]+[0-9]*$/],
            msg: ","
          }
        },
        nickname: {
          regex: {
            params: [/^[a-zA-Z]+[0-9]*$/],
            msg: ","
          }
        },
        password: {
          required: {
            msg: ""
          },
          lengthBetween: {
            params: [6, 32],
            msg: "632"
          },
          regex: {
            params: [/(?=.*[0-9])(?=.*[a-zA-Z])/],
            msg: ""
          }
        }
      }
    };
  },

};
</script>

