import api from "./api"

// 
let is = {
    /**
     * 
     * @param {number} value
     * @return {boolean}
     */
    numeric(value) {
        if (!/^\d+$/.test(value.toString()))
            return false;
        return true;
    },

    required(value) {
        return !!(value || value === 0);
    },

    /**
     * 
     * @param {number} value
     * @param {number} comparison 
     * @return {boolean}
     */
    min(value, comparison) {
        if (value < comparison)
            return false;
        return true;

    },

    /**
     * 
     * @param {number} value
     * @param {number} comparison 
     * @return {boolean}
     */
    max(value, comparison) {
        if (value > comparison)
            return false;
        return true;

    },

    /**
     * 
     * @param {number} value
     * @param {number} min
     * @param {number} max
     * @return {boolean}
     */
    between(value, min, max) {
        if (
            !this.min(value, min) ||
            !this.max(value, max)
        )
            return false;
        return true;

    },

    /**
     * 
     * @param {number} value
     * @return {boolean}
     */
    positive(value) {
        if (!this.numeric(value))
            return false;

        if (value <= 0)
            return false;
        return true;

    },

    /**
     * 
     * @param value
     * @return {boolean}
     */
    negative(value) {
        if (!this.numeric(value))
            return false;

        if (value >= 0)
            return false;
        return true;

    },

    /**
     * 
     * @param {string} value
     * @param {number} comparison
     * @return {boolean}
     */
    minLength(value, comparison) {
        if (value.length < comparison)
            return false;
        return true;

    },

    /**
     * 
     * @param {string} value
     * @param {number} comparison
     * @return {boolean}
     */
    maxLength(value, comparison) {
        if (value.length > comparison)
            return false;
        return true;

    },

    /**
     * 
     * @param {string} value
     * @param {number} min
     * @param {number} max
     * @return {*|boolean}
     */
    lengthBetween(value, min, max) {
        if (!this.minLength(value, min) ||
            !this.maxLength(value, max))
            return false;
        return true;

    },

    /**
     * 
     * @param {string} value
     * @param {string} comparison
     * @return {*|boolean}
     */
    startsWith(value, comparison) {
        if (!value.startsWith(comparison))
            return false;
        return true;

    },

    /**
     * 
     * @param {string} value
     * @param {string} comparison
     * @return {*|boolean}
     */
    endsWith(value, comparison) {
        if (!value.endsWith(comparison))
            return false;
        return true;

    },

    includes(value, comparison) {
        if (!value.includes(comparison))
            return false;
        return true;

    },

    /**
     * 
     * @param {mix} value
     * @param {Array} comparison
     */
    in(value, comparison) {
        if (comparison.indexOf(value) === -1)
            return false;
        return true;

    },

    /**
     * 
     * @param {string} value
     * @return {boolean}
     */
    email(value) {
        let re = /^\w+@\w+\.\w+$/;
        if (!re.test(value))
            return false;
        return true;

    },

    /**
     * 
     * @param {string} value
     * @return {boolean}
     */
    username(value) {
        let re = /^[a-zA-Z0-9]\w+$/;
        if (!re.test(value))
            return false;
        return true;

    },

    /**
     * 
     * @param {string} value
     * @param {string} country 
     * @return {boolean}
     */
    phone(value, country = 'zh') {
        let re;

        switch (country) {
            case 'zh':
                re = /^(?:\+?(?:86))?(\s|-)?1\d{10}$/;
                break;
        }

        if (!re.test(value))
            return false;
        return true;

    },

    /**
     * 
     * @param value
     * @param re 
     * @return {boolean}
     */
    regex(value, re) {
        if (typeof re == 'string')
            re = new RegExp(re);

        if (!re.test(value))
            return false;

        return true;
    },

    //
    unique(value, model, action, property) {
        return api(`${model}/${action}`, { where: { and: { [property]: value } } }).then(r => {
            return !r.data;
        })
    }
};

// export default is;

export function call(type, value, ...args) {
    // ,
    if (type !== "required" && !value && value !== 0) {
        return true;
    }

    return is[type](value, ...args);
}

