<template>
  <div class="setting">
    <div class="header">
      <div class="title"></div>
      <div class="edit" @click="editOrCancel" v-bind:class="{editActive: editModel}">
        <span v-if="editModel"></span>
      </div>
    </div>
    <fieldset :disabled="updatePending">
      <table>
        <tbody>
          <tr>
            <td></td>
            <td>
              <span v-if="!me.nickname && !editModel">--</span>
              <input
                v-if="editModel|| me.nickname"
                @focus="error.nickname=false"
                type="text"
                v-model="me.nickname"
                :readonly="!editModel"
              >
            </td>
            <td>
              <span v-if="error.nickname" class="error">20</span>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <span v-if="!me.username && !editModel">--</span>
              <input
                v-if="editModel || me.username"
                @focus="error.username=false"
                type="text"
                v-model="me.username"
                :readonly="!editModel"
              >
            </td>
            <td>
              <span v-if="error.username" class="error">{{error.username}}</span>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <span v-if="!me.info && !editModel">--</span>
              <input
                v-if="editModel|| me.info"
                @focus="error.info=false"
                type="text"
                v-model="me.info"
                :readonly="!editModel"
              >
            </td>
            <td>
              <span v-if="error.info" class="error">50</span>
            </td>
          </tr>
          <tr v-if="editModel">
            <td></td>
            <td>
              <button @click="validateAndUpdate"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </fieldset>
  </div>
</template>

<script>
import store from "../../lib/store";
import api from "../../lib/api";

export default {
  data() {
    return {
      me: {}, //
      saveMe: {}, //
      editModel: false, //
      updatePending: false, //
      error: {
        username: false,
        nickname: false,
        info: false
      }
    };
  },

  mounted() {
    this.me = store.get("user");
    this.saveMe = { ...this.me };
  },

  methods: {
    editOrCancel() {
      this.editModel = !this.editModel;
      this.error.username = false;
      this.me = store.get("user");
    },

    validateAndUpdate() {
      //,,
      if (
        this.inValidateUsername() ||
        this.inValidateNickname() ||
        this.inValidateInfo()
      ) {
        return;
      }

      this.error.nickname = this.error.username = this.error.info = false;

      //
      if(this.saveMe.nickname === this.me.nickname && this.saveMe.username === this.me.username &&this.saveMe.info === this.me.info) {
        this.editModel = false;
        return;
      }

      //
      this.updatePending = true;
      let param = { where: { and: { username: this.me.username } } };
      api("user/exists", param).then(r => {
        let usernameChange = this.saveMe.username !== this.me.username;

        if (r.data && usernameChange) {
          this.updatePending = false;
          this.error.username = "";
          return;
        } else {
          this.updateUsername();
        }
      });
    },

    //
    updateUsername() {
      api("user/update", this.me).then(r => {
        if (r.success) {
          this.me = r.data;
          store.set("user", r.data);
          this.editModel = false;
          this.updatePending = false;
          window.location.reload();
        }
      });
    },

    inValidateNickname() {
      return (this.error.nickname = this.me.nickname.length > 20);
    },

    inValidateUsername() {
      if (
        !this.me.username ||
        /[\W_]/.test(this.me.username) ||
        this.me.username.length < 4 ||
        this.me.username.length > 20
      ) {
        this.error.username = ",420";
        return true;
      }
    },

    inValidateInfo() {
      return (this.error.info = this.me.info.length > 50);
    }
  }
};
</script>

<style>
</style>
